
































import { Component, Prop, Vue, Watch } from 'vue-property-decorator'

@Component({
  components: {}
})
export default class MyComponent extends Vue {
  /* data */
  isShowDebug = !this.$store.state.isProdEnvUrl
  visible = false
  ruleForm: any = {
    domainApi: this.$store.state.domainApi,
  }
  rules: {} = {
    // domainApi: {
    //   required: true, 
    //   trigger: 'blur', 
    //   message: '请输入'
    // },
  }
  domainNodeServer = '' // 本机中层
  debugNodeServer = this.$store.state.debugNodeServer.slice(2) // 线上调试中层

  /* lifecycle hook */
  created(){
    const middleServerPort = location.protocol ==='https:' ? ':5001' : ':5000';
    this.domainNodeServer = location.port ? (location.hostname + middleServerPort) : location.host;
  }

  /* method */
  submitForm(formName: string) { // 表单提交校验
    (this.$refs[formName] as any).validate((valid: boolean) => {
      if (valid) {
        this.visible = false;
        this.$store.commit('setDomainApi',this.ruleForm.domainApi)
      }
    })
  }
}
