import axios, { AxiosResponse, AxiosRequestConfig } from 'axios'
// import { Toast } from 'cube-ui'
import {Message,Loading} from 'element-ui'
import store from '../../../store'
import router from '../../../router'

let loadingInstance:any;
// 创建axios的实例
const service = axios.create({
  timeout: 1000000 // 超时时间
})

// 请求拦截
service.interceptors.request.use(
  (config: AxiosRequestConfig) => {
    // (Toast as any).$create({
    //   time: 0 // 设置为0时永不关闭，默认为3000
    // }).show()
    loadingInstance = Loading.service({background: 'transparent'}) // 打开请求中弹窗
    return config;
  },
  (err: any) => {
    Promise.reject(err);
  }
);

// 响应拦截
service.interceptors.response.use(
  (response: AxiosResponse) => {
    // console.log('response',response);
    // (Toast as any).$create().hide(); // 关闭请求中弹窗
    loadingInstance.close() // 关闭请求中弹窗
    const data = response.data
    if( // 默认错误提示弹窗
      (response.config as any).url.indexOf('/api/3.0') >=0 
      && data.resCode !== '0000'
    ){
      // (Toast as any).$create({
      //   type: 'txt',
      //   txt: data.resMsg || data.err || '请求错误',

      // }).show();
      Message.error(data.resMsg || data.err || '请求错误')

      if(data.resCode === 'E0000002'){  // token失效或缺失
        store.commit('resetAll')
        router.replace('/user/login')
      }
      else if(data.resCode === 'E1010019'){ // 未开户
        setTimeout(()=>{
          // location.href = '/account/lmAccount#open'
          router.replace('/account/lmAccount#open')
        },2000)
      }
    }
    return data;
  },
  (err: any) => {
    // (Toast as any).$create({
    //   type: 'txt',
    //   txt: `请求错误: ${err || err.response}`
    // }).show()
    loadingInstance.close() // 关闭请求中弹窗
    Message.error(`请求错误: ${err || err.response}`)
    // console.log('err',err)
    // console.log('err.response',err || err.response)

    return err.response || {}
  }
)


// export default service

/**
 * 请求插件
 * @param {*} options 
 */
export default function request(options: any) {
  const {
    // api 请求方式
    api,
    params,
    method = 'post',

    // axios 原生请求方式
    callback, // 直接回调方法。非async模式
    url, // 全路径url
    direct, // 相对路径
    data, // 未经处理的数据

    // 表单中间页 请求方式
    formSubmitData,
    withCredentials = false, // 允许携带cookie
  } = options

  // 表单提交：中间页面（自身公用页&第三方）
  if(formSubmitData){
    formSubmit(formSubmitData)
    return
  }

  /**
   * 当调试服务修改为线上api,中间层接口仍请求线上调试node服务
   */
  let requestServer = '';
  if( // 若为restful接口，经过中层转发
    store.state.domainApi === 'www.daokoudai.com'
    && direct && direct.indexOf('api') === 0
    && (direct.indexOf('3.0') <0 && direct.indexOf('2.0') <0)
  ){
    requestServer = store.state.debugNodeServer;
    console.log('requestServer',requestServer)
  } else {
    requestServer = store.state.domainNodeServer;
  }

  return (service as any)[method](
    // url
    url ? url : `${requestServer}/` + (direct || 'api/3.0/m/' + api),
    // params
    data ? ((method === 'delete' || method === 'get') ? {params: data} : data )  : dealFormData({
      params: encodeURI(JSON.stringify(params)), // 防止中文编码错误采用encodeURI处理
      from: 'M',
      imei: 'imei',
      appKey: '99999999',
    }).replace(/\+/g, '%2B'), // +号被转换为空格问题兼容(base64编码中常见) 
    /**
     * 参见：
     * https://www.cnblogs.com/panie2015/p/5885922.html
     * https://www.cnblogs.com/zxgy/articles/10222087.html
     */
  )
}


// 
function dealFormData(data: any) {
  let keys = Object.keys(data),
    dataStr = '';
  keys.forEach((curr, index) => {
    dataStr += `${curr}=${data[curr]}&`
  })
  return dataStr.slice(0, -1)
}
// 跳转上行网付中间页表单
function formSubmit (data:any) {
  let form = document.createElement('form')
  let params = data.params

  form.method = data.method || 'post'
  form.action = data.shUrl || data.url

  for(var item in params){
      let input = document.createElement('input')  
      input.value = typeof params[item] == 'object' // 兼容一层参数为对象格式
          ? JSON.stringify(params[item])
          : params[item]
      input.value = params[item]
      input.name = item
      input.type = 'hidden'

      form.appendChild(input)
  }
  // ajax回调中submit兼容
  // chrome浏览器:Form submission canceled because the form is not connected
  document.body.appendChild(form);

  form.submit();
}