// 多页面history兼容(开发环境)
const projectNamePathIndex = (location.pathname).indexOf('.html')
let base = process.env.BASE_URL;
if(projectNamePathIndex >= 0 ){
  base = `/${location.pathname.slice(1,projectNamePathIndex)}.html`
}

// console.log('base',base)
// const router = location.host === "doudizhu.github.io"
const router = store.state.tld === "github.io"
  ? new Router({
    base,
    routes,
  })
  : new Router({
    mode: 'history',
    base,
    routes,
  })

// 路由守卫
router.beforeEach((to,from,next)=>{
  const isLogin = store.state.token ? true : false;
  /* 页面路由守卫：判断：是否需要登录访问 */
  if (to.meta.requireAuth === false) { // 不需要登录访问
    next()
  } 
  else { // 需要登录访问
    if (store.state.token) { // 含有token
      // 权限判断
      const role = (store.state.user as any).role
      if(hasPermission(to,role)){
        next()
      }else{
        next('/404') // 没有权限进入
      }
    } else {
      // next()
      Message.error('登录状态失效，请重新登录');
      store.commit('resetAll')
      next({
        path: '/login',
        query: {redirect: to.fullPath},
      })
    }
  }
})

export default router


/**
 * 判断是否有权限
 * @param roles 当前角色
 * @param route 当前路由对象
 */
function hasPermission(route:any,role:string){
  if(route.meta && route.meta.roles) {// 是否meta.roles包含角色的key值，如果包含那么就是有权限，否则无权限
    return (route.meta.roles.indexOf(role) >= 0);
  }else{ // 默认不设置有权限
    return true;
  }
}